<template>
    <yd-layout class="schBg">
        <div v-if="firstF" v-show="treeFlag">
            <div class="tree-menu">
                <trs-tree :type="type" v-on:callbackTop="ShareMeeting" @click="jiekouFlag = !jiekouFlag;"></trs-tree>
            </div>
        </div>
        <yd-tabbar slot="tabbar" bgcolor="#fff" v-if="treeFlag">
            <yd-button v-on:click.native="share()" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">确定</yd-button>
        </yd-tabbar>
        <div v-show="!treeFlag">
            <div class="handleTabBox" v-show="activeFlag">
                <yd-flexbox>
                    <yd-flexbox-item>
                        <div class="tabTopL" v-text="day.split('月')[0]+'月'"></div>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <div @click="closeWebView()" class="tabTopR"><img class="imgWork" src="../../../static/images/home@2x.png">工作中心</div>
                    </yd-flexbox-item>
                </yd-flexbox>
                <div class="tabBottom">
                    <router-link to="/handle"><span class="tabIcon">待办</span></router-link>
                    <router-link to="/performanceList"><span class="tabIcon">绩效</span></router-link>
                    <router-link to="/meetingManage"><span class="tabIcon">会议</span></router-link>
                    <router-link to=""><span class="tabIcon active">日程</span></router-link>
                </div>
            </div>
            <div class="otherTitle" v-show="!activeFlag">
                <div class="left">
                    <img src="../../../static/images/schedule/s_1.png" />
                    <h3>{{schName}}的日程</h3>
                </div>
                <div class="right">
                    <div class="tabTopL" v-text="day.split('月')[0]+'月'"></div>
                </div>
            </div>
            <div class="schedules">
                <div class="myDate" :class="{dateClose:closeIndex==0&&dateClose==true,dateClose1:closeIndex==1&&dateClose,dateClose2:closeIndex==2&&dateClose,dateClose3:closeIndex==3&&dateClose,dateClose4:closeIndex==4&&dateClose,dateClose5:closeIndex==5&&dateClose}">
                    <div id="js_myDate" placeholder="Please Select..."></div>
                    <div class="btnDataContent" v-on:click="dateClose=!dateClose">
                        <img src="../../../static/images/schedule/s_16.png" class="btnDate" v-if="!dateClose" />
                        <img src="../../../static/images/schedule/s_15.png" class="btnDate" v-if="dateClose" />
                    </div>
                </div>
                <div class="mainContent pubList">
                    <div class="newSchdlue" v-if="activeFlag&&newSchId!=''">
                        <div class="news" @click="$router.push('/detailsSchedule/' + newSchId)">
                            <span class="redCircle"></span>
                            <span class="text">您有新日程</span>
                            <span><img class="arrowImg" src="../../../static/images/arrow@2x.png"></span>
                        </div>
                    </div>
                    <div class=" handleList" v-if="allScheduleArr.length<1">
                        <div class="noBox">
                            <img src="../../../static/images/label/noHandle.png">
                        </div>
                        <p class="noWord">偷得浮生半日闲</p>
                        <p class="noWord">今日暂无工作安排</p>
                    </div>
                    <div v-if="activeFlag" class="scheduleDetail" v-for="item in allScheduleArr">
                        <div class="topMess" @click="$router.push('/detailsSchedule/' + item.id)">
                            <div class="title" v-text="item.title"></div>
                            <span class="status now" v-show="item.betweenTime===0">现在</span>
                            <span class="status" v-show="item.betweenTime>0&&item.betweenTime<60">{{item.betweenTime}}分钟后</span>
                            <span class="status" v-show="item.betweenTime>=60&&item.betweenTime<60*24">{{parseInt(item.betweenTime/60)}}小时后</span>
                            <span class="status" v-show="item.betweenTime>=24*60">{{parseInt(item.betweenTime/1440)}}天后</span>
                            <div class="time">{{'时间：'+dateComcomputed(item.begintime,item.endtime)}}</div>
                            <div class="add" v-show="item.address!=''&&item.address!=undefined">地点：{{item.address}}</div>
                        </div>
                        <div class="bottomBox">
                            <ul>
                                <li class="share" @click="treeFlag=true;firstF=true;shareTitle=item.title;shareTime=dateComcomputed(item.begintime,item.endtime);shareAddress=item.address">分享</li>
                                <li class="edit" @click="toUpdata(item.id)">编辑</li>
                                <li class="delete" @click="show=true;delSchId=item.id">删除</li>
                            </ul>
                        </div>
                    </div>
                    <!-- 查看他人日程列表 -->
                    <div class="scheduleDetail scheduleDetail1" v-if="!activeFlag&&item.id!=undefined" v-for="item in allScheduleArr">
                        <div class="topMess">
                            <div class="title" v-text="item.title"></div>
                            <div class="time">{{'时间：'+dateComcomputed(item.begintime,item.endtime)}}</div>
                            <div class="add" v-show="item.address!=''&&item.address!=undefined">地点：{{item.address}}</div>
                        </div>
                    </div>
                    <!-- <div v-if="activeFlag&&allScheduleArr.length!=0" class="listDate"><span class="dataText">{{ day }}</span></div> -->
                </div>
                <div class="scheduleCalendarBox" v-if="!activeFlag&&otherTimeSch&&allScheduleArr.length>0">
                    <div class="noticTitle">
                        <span class="bgCot"></span>
                        <p>代表已有工作安排</p>
                    </div>
                    <div class="scheduleCalendar">
                        <ul>
                            <li v-for="n in 10">
                                <div class="leftc" v-text="time(n)"></div>
                                <div class="rightc">
                                </div>
                            </li>
                            <li class="noPo" v-for="(item, index) in allScheduleArr">
                                <div :style="styleObj[index]" class="hasSchedule aa bb"></div>
                            </li>
                            <li :id="`curLi`" ref="curLi" :style="curStyle" class="currentTime">
                                <div class="leftc" v-text="currTime"></div>
                                <div class="rightc">
                                    <div class="cirRed"></div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <!-- <div class="goBack" v-if="false">
                    <div class="backBtn">返回</div>
                </div> -->
            </div>
        </div>
        <yd-tabbar v-if="activeFlag&&treeFlag==false" class="schedules" slot="tabbar" bgcolor="#F4F4F2" activeColor="#59A5FD" color="#999999">
            <yd-tabbar-item :link="'/mySchedule/' + $store.getters.getUserInfo.empcode + '/0/my'" title="我的日程" @click.native="toSelf" :active="activeFlag">
                <yd-icon name="mySchedule" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item class="addSchedule11" :class="{addSchedule12: bottomPopup && hasSelect,addSchedule13: !bottomPopup,btnActive1:bottomPopup}" link="" @click.native="editable.length==0?$router.push('/addeditSchedule/0/self'):animation()">
                <yd-icon name="addBtn" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="查看他人日程" link="/viewSchedules/" :active="!activeFlag">
                <yd-icon name="toView" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <div v-if="hasSelect" class="noSelect" :class="{closeBtn: bottomPopup, selectBtn: !bottomPopup}">
                <div class="btnLeft" @click="setTitleName('新建日程'); $router.push('/addeditSchedule/0/self')">
                    <img slot="icon" src="../../../static/images/schedule/s_13.png" />
                    <div slot="text" class="slotText">为自己新建</div>
                </div>
                <div class="btnRight" @click="setTitleName('新建他人日程'); $router.push('/addeditSchedule/0/other')" v-show="editable.length!=0">
                    <img slot="icon" src="../../../static/images/schedule/s_14.png">
                    <div slot="text" class="slotText">为他人新建</div>
                </div>
            </div>
        </yd-tabbar>
        <yd-popup v-model="show" position="center" width="5.79rem">
            <div class="other delother">是否确认删除</div>
            <div class="otherBtn">
                <ul>
                    <li class="yes" @click="show=false;deleteCalendar()">是</li>
                    <li class="no" @click="show=false">否</li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import $ from '../../jquery-vendor.js';
import trsTree from '../../components/util/trsTree/trsTree.vue';
import 'mobiscroll';
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'mySchedule',
    components: {
        trsTree
    },
    data() {
        return {
            bottomPopup: true,
            closeIndex: 0,
            dateClose: true,
            scheduleArr: [],
            schName: 'my',
            allScheduleArr: [],
            onceFlag: false,
            schId: 0,
            pointParams: {},
            hasSelect: false,
            activeFlag: true, // 判断是否是他人的日程
            jurisdictionFlag: true, // 判断是否有权限
            currTime: '',
            day: '',
            hours: '',
            show: false,
            delSchId: 0,
            searchDate: '',
            searchDate1: '',
            newSchId: '', // 最新一条日程ID
            styleObj: [],
            curStyle: {},
            otherTimeSch: false,
            treeFlag: false,
            type: 'share',
            shareId: '',
            shareTitle: '',
            shareTime: '',
            shareAddress: '',
            firstF: false,
            editable: [] // 可新建人列表
        }
    },
    mounted() {
        document.setTitle('我的日程');
        const vm = this;
        vm.searchDate1 = new Date().getTime();
        vm.schId = vm.$route.params.schId;
        if (this.$route.params.schFlag == 0) {
            vm.activeFlag = true;
        } else {
            vm.activeFlag = false;
        }
        vm.pointParams = {
            ssotoken: vm.getUserInfo.empcode,
            year: new Date().getFullYear(),
            month: new Date().getMonth() + 1
        }
        vm.schName = vm.$route.params.schName;
        vm.getSchedules(1);
        vm.getDay();
        vm.getNewSchId();
        vm.nowDate();
        vm.getEditablePerson();
        vm.searchDate = vm.formatDate(new Date(), 'yyyy-MM-dd');
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    watch: {
        searchDate: function () {
            let vm = this;
            vm.setScheduleTime({
                scheduleTime: vm.searchDate
            });
        }
    },
    methods: {
        ...mapMutations([
            'setTitleName', 'setScheduleId', 'setScheduleInfo', 'setScheduleTime'
        ]),
        // 日程下面的点
        getPoint(params) {
            const vm = this;
            return new Promise((resolve, reject) => {
                vm.get(vm.conf.schedule + '/mobileInterface/calendarUrgentByMonth', params, function (res) {
                    resolve(res);
                });
            });
        },
        nowDate() {
            const vm = this;
            let now = new Date();
            let nowDate = now.getTime();
            let minDate = now.getMinutes();
            let startDate = now.setHours(9, 0, 0, 0);
            let height = 1.12;
            let offsetTop = parseInt((nowDate - startDate) / 1000 / 60); // 距离9点有多少分钟
            vm.currTime = vm.time(parseInt(offsetTop / 60) + 1) + minDate;
            vm.curStyle = { top: `${offsetTop / 60 * height}rem` };
            // vm.$refs.curLi[0].style.top = `${offsetTop / 60 * height}rem`;
            /* setInterval(() => {
                let now = new Date();
                let nowDate = now.getTime();
                let minDate = now.getMinutes();
                let startDate = now.setHours(9, 0, 0, 0);
                let offsetTop = parseInt((nowDate - startDate) / 1000 / 60); // 距离9点有多少分钟
                vm.currTime = vm.time(parseInt(offsetTop / 60) + 1) + minDate;
                vm.$refs.curLi.style.top = `${offsetTop / 60 * height}px`;
            }, 1000 * 60); */
        },
        toSelf() {
            const vm = this;
            vm.schId = vm.$store.getters.getUserInfo.ssotoken;
            vm.activeFlag = true;
            vm.schName = 'my';
            vm.getSchedules();
        },
        toUpdata(id) {
            var vm = this;
            vm.setTitleName('日程详情');
            vm.setScheduleId(id);
            vm.$router.push(`/addeditSchedule/${id}/edit`)
        },
        toDetail(id) {
            var vm = this;
            // vm.setScheduleId(id);
            vm.$router.push(`/detailsSchedule/${id}/`)
        },
        dateComcomputed: function (begintime, endtime) {
            let bdate = new Date(begintime);
            let edate = new Date(endtime);
            return this.formatDate(bdate, 'yyyy-MM-dd hh:mm') + ' - ' + this.formatDate(edate, 'hh:mm');
        },
        otherDate(begintime, endtime, index) {
            const vm = this;
            let startDate = vm.searchDate1;
            let height = 1.12;
            let offsetBTop = parseInt((begintime - startDate) / 1000 / 60); // 开始时间距离9点有多少分钟
            let offsetETop = parseInt((endtime - startDate) / 1000 / 60); // 结束时间距离9点有多少分钟
            vm.styleObj[index] = { top: `${offsetBTop / 60 * height}rem`, height: `${(offsetETop - offsetBTop) / 60 * height}rem` };
            /* vm.$refs[`timeDiv${index}`][0].style.top = `${offsetBTop / 60 * height}px`; // div的top值
            vm.$refs[`timeDiv${index}`][0].style.height = `${(offsetBTop - offsetETop) / 60 * height}px`; // div的高度 */
        },
        getSchedules(n) {
            var vm = this;
            // 判断是否是自己的日程
            if (vm.activeFlag) {
                vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/myCalendar', {
                    'ssotoken': vm.getUserInfo.empcode,
                    'date': vm.searchDate
                }, function (res) {
                    vm.allScheduleArr = res.jsonData;
                    if (n == 1) {
                        vm.getPoint(vm.pointParams).then((res) => {
                            vm.selectTime(res.jsonData);
                        });
                    }
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/viewOtherCalendar', {
                    'otherEmpcode': vm.schId,
                    'date': vm.searchDate,
                    'ssotoken': vm.getUserInfo.empcode
                }, function (res) {
                    vm.allScheduleArr = res.jsonData;
                    if (n == 1) {
                        vm.pointParams.otherEmpcode = vm.schId;
                        vm.getPoint(vm.pointParams).then((res) => {
                            vm.selectTime(res.jsonData);
                        });
                    }
                    if (vm.allScheduleArr.length > 0 && vm.allScheduleArr[0].id == undefined) {
                        vm.otherTimeSch = true;
                        vm.allScheduleArr.forEach((item, index) => {
                            vm.otherDate(item.begintime, item.endtime, index);
                        });
                    }
                });
            }
        },
        getDay() {
            this.day = new Date().getFullYear() + '年' + (new Date().getMonth() + 1) + '月' + new Date().getDate() + '日';
            this.hours = new Date().getHours();
        },
        shareFun(title, id) {
            let info = {};
            info.title = title;
            info.empcode = 0;
            this.setScheduleId(id);
            this.setScheduleInfo(info);
            this.$router.push('/shareCntacts');
        },
        time(num) {
            if (num < 4) {
                return `上午${num + 8}时`;
            } else if (num == 4) {
                return `正午`;
            } else {
                return `下午${num - 4}时`;
            }
        },
        selectTime(pointData) {
            let vm = this;
            $('#js_myDate').mobiscroll().calendar({
                display: 'inline',
                layout: 'liquid',
                theme: 'ios',
                lang: 'zh',
                // yearChange: false,
                max: new Date(new Date().getFullYear() + 100, new Date().getMonth(), new Date().getDate()),
                markedDisplay: 'bottom',
                marked: [],
                onInit: function (event, inst) {
                    // 初始化时执行
                },
                onMarkupReady: function (event, inst) { },
                onBeforeShow: function (event, inst) {
                    // 初始化时执行
                    var begin = new Date(inst._tempValue + ' 00:00').getTime();
                    var end = new Date(inst._tempValue + ' 23:59:59').getTime();
                    for (var i = 0; i < vm.allScheduleArr.length; i++) {
                        if (vm.allScheduleArr[i].begintime > begin && vm.allScheduleArr[i].endtime < end) {
                            vm.scheduleArr.push(vm.allScheduleArr[i]);
                        }
                    }
                    pointData = pointData.sort(vm.compare('urgentflg'));
                    pointData.forEach((item, index) => {
                        if (item.urgentflg == 1) {
                            inst.settings.marked.push({
                                d: new Date(item.date),
                                color: 'red'
                            });
                        } else {
                            inst.settings.marked.push({
                                d: new Date(item.date),
                                color: 'rgb(34, 139, 34)'
                            });
                        }
                    });
                    inst.redraw(); // 添加remark点
                },
                onPosition: function (event, inst) { },
                onShow: function (event, inst) {
                    var day = inst._wheelArray[2];
                    // 月份加载时候执行 , 可获取年月时间
                    var rows = $(event.target).find('.mbsc-cal-slide-a .mbsc-cal-row');
                    var timer = null;
                    rows.each(function (i, element) {
                        $(this).find('.mbsc-cal-day-i').each(function (j, el) {
                            if (vm.onceFlag) {
                                return false;
                            }
                            if ($(el).text() == day) {
                                var index = $(this).closest('.mbsc-cal-row').index();
                                // var tempData = rows.eq(1).find('.mbsc-cal-day-i').eq(0).text();
                                vm.onceFlag = true;
                                clearTimeout(timer);
                                timer = setTimeout(function () {
                                    vm.closeIndex = index;
                                }, 100);
                                return false;
                            }
                        });
                    });
                },
                onDestroy: function (event, inst) { },
                onClose: function (event, inst) { },
                onBeforeClose: function (event, inst) { },
                onMonthLoading: function (event, inst) {
                    // // 月份加载时候执行 , 可获取年月时间
                    // var index = $(event.target).closest('.mbsc-cal-row').index();
                    // setTimeout(function () {
                    //     vm.closeIndex = index;
                    // }, 100);
                },
                onMonthLoaded: function (event, inst) { },
                onMonthChange: function (event, inst) {
                    // 选择月份时执行
                    vm.day = event.year + '年' + (event.month + 1) + '月';
                    vm.pointParams.year = event.year;
                    vm.pointParams.month = event.month + 1;
                    if (!vm.activeFlag) {
                        vm.pointParams.otherEmpcode = vm.schId;
                    }
                    vm.getPoint(vm.pointParams).then((res) => {
                        res.jsonData = res.jsonData.sort(vm.compare('urgentflg'));
                        res.jsonData.forEach((item, index) => {
                            if (item.urgentflg == 1) {
                                inst.settings.marked.push({
                                    d: new Date(item.date),
                                    color: 'red'
                                });
                            } else {
                                inst.settings.marked.push({
                                    d: new Date(item.date),
                                    color: 'rgb(34, 139, 34)'
                                });
                            }
                        });
                        inst.redraw(); // 添加remark点
                    });
                    console.log(inst.settings.marked)
                },
                onEventSelect: function (event, inst) { },
                onDayChange: function (event, inst) {
                    // 选择天数时执行
                    var index = $(event.target).closest('.mbsc-cal-row').index();
                    setTimeout(function () {
                        vm.closeIndex = index;
                        var dayArr = inst._tempValue.split('/').join('-');
                        // var dayArr = inst._tempValue.split('/');
                        vm.allScheduleArr = [];
                        vm.searchDate = dayArr;
                        console.log(vm.searchDate);
                        vm.searchDate1 = new Date(inst._tempValue + ' 09:00').getTime();
                        vm.getSchedules(2);
                        vm.nowDate();
                    }, 100);
                    // Update marked array
                }
            });
        },
        compare(prop) {
            return function (obj1, obj2) {
                var val2 = obj1[prop];
                var val1 = obj2[prop];
                if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
                    val1 = Number(val1);
                    val2 = Number(val2);
                }
                if (val1 < val2) {
                    return -1;
                } else if (val1 > val2) {
                    return 1;
                } else {
                    return 0;
                }
            }
        },
        // createSelf() {
        //     var vm = this;
        //     vm.get(vm.conf.schedule + '/mobileInterface/addMyCalendar', {
        //         'ssotoken': vm.getUserInfo.empcode
        //     }, function (res) {});
        // },
        // 删除日程
        deleteCalendar() {
            var vm = this;
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/deleteCalendar', {
                'id': vm.delSchId,
                'ssotoken': vm.getUserInfo.empcode
            }, function (res) {
                vm.$dialog.toast({
                    mes: '删除日程成功',
                    timeout: 2000,
                    callback: () => {
                        vm.scheduleArr = [];
                        vm.getSchedules();
                    }
                });
            });
        },
        // 获取最新一条日程id
        getNewSchId() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/findLatestUnReadCalendarId', {
                'ssotoken': vm.getUserInfo.empcode
            }, function (res) {
                if (res.jsonData != undefined && res.jsonData != null && res.jsonData != '') {
                    vm.newSchId = res.jsonData;
                }
            });
        },
        ShareMeeting(json) {
            let vm = this;
            if (vm.shareId == '') {
                let idArr1 = [];
                for (let key in json) {
                    idArr1.push(key);
                    for (let key1 in json[key]) {
                        for (let i = 0; i < idArr1.length; i++) {
                            if (idArr1[i] == key && json[key][key1] == 0) {
                                idArr1.splice(i, 1);
                            }
                        }
                    }
                }
                vm.shareId = idArr1.join(',');
            } else {
                let idArr = [];
                idArr = vm.shareId.split(',');
                for (let key in json) {
                    idArr.push(key);
                    idArr = Array.from(new Set(idArr));
                    for (let key1 in json[key]) {
                        for (let i = 0; i < idArr.length; i++) {
                            if (idArr[i] == key && json[key][key1] == 0) {
                                idArr.splice(i, 1);
                            }
                        }
                    }
                }
                vm.shareId = idArr.join(',');
            }
        },
        share() {
            let vm = this;
            if (vm.shareId == '') {
                // vm.$dialog.toast({
                //     mes: '请选择分享人员',
                //     timeout: 2000
                // });
                vm.treeFlag = false;
            } else {
                let promise = new Promise(function (resolve, reject) {
                    vm.get(vm.conf.uchatMessage + '/sendMessageApi', {
                        'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                        'title': '日程分享',
                        'summary': '日程主题：' + vm.shareTitle + '\n' + '\n' + '会议时间：' + vm.shareTime + '\n' + '\n' + '会议地点：' + vm.shareAddress,
                        'url': '',
                        'empcode': vm.shareId
                    }, function (res) {
                        vm.$dialog.toast({
                            mes: '分享成功',
                            timeout: 2000
                        });
                        resolve(true);
                    })
                });
                promise.then(function (data) {
                    if (data) {
                        vm.treeFlag = !vm.treeFlag;
                    }
                });
            }
        },
        // 判断是否有权限为他人新建
        getEditablePerson() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/Calendar/mobileInterface/getEditablePerson', {
                'empcode': vm.getUserInfo.empcode
            }, function (res) {
                vm.editable = res.jsonData;
            });
        },
        animation() {
            let vm = this;
            vm.bottomPopup = !vm.bottomPopup;
            vm.hasSelect = true;
        }
    }
}
</script>
